function myFunction() {
    let winScroll = document.body.scrollTop || document.documentElement.scrollTop;
    let height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
    let scrolled = (winScroll / height) * 100;
    scrolled = scrolled.toFixed(1);
    // console.log((scrolled))

    line2.classList.remove('filled-marked');
    line3.classList.remove('filled-marked');
    line4.classList.remove('filled-marked');
    line5.classList.remove('filled-marked');
    line6.classList.remove('filled-marked');


    if(scrolled < 16) {

        line1.style.backgroundColor = '#2D9CDB';
        line1.style.opacity = '1';
        line2.style.backgroundColor = '#333333';
        line2.style.opacity = '0.5';
        line3.style.backgroundColor = '#333333';
        line3.style.opacity = '0.5';
        line4.style.backgroundColor = '#333333';
        line4.style.opacity = '0.5';
        line5.style.backgroundColor = '#333333';
        line5.style.opacity = '0.5';
        line6.style.backgroundColor = '#333333';
        line6.style.opacity = '0.5';
    } else
        if(scrolled > 16 && scrolled < 32) {

            line1.style.backgroundColor = '#333333';
            line1.style.opacity = '0.5';
            line2.style.backgroundColor = '#2D9CDB';
            line2.style.opacity = '1';
            line3.style.backgroundColor = '#333333';
            line3.style.opacity = '0.5';
            line4.style.backgroundColor = '#333333';
            line4.style.opacity = '0.5';
            line5.style.backgroundColor = '#333333';
            line5.style.opacity = '0.5';
            line6.style.backgroundColor = '#333333';
            line6.style.opacity = '0.5';



    } else if(scrolled > 32 && scrolled < 48) {
            line1.style.backgroundColor = '#333333';
            line1.style.opacity = '0.5';
            line2.style.backgroundColor = '#333333';
            line2.style.opacity = '0.5';
            line3.style.backgroundColor = '#2D9CDB';
            line3.style.opacity = '1';
            line4.style.backgroundColor = '#333333';
            line4.style.opacity = '0.5';
            line5.style.backgroundColor = '#333333';
            line5.style.opacity = '0.5';
            line6.style.backgroundColor = '#333333';
            line6.style.opacity = '0.5';

    } else if(scrolled > 48 && scrolled < 64) {
            line1.style.backgroundColor = '#333333';
            line1.style.opacity = '0.5';

            line2.style.backgroundColor = '#333333';
            line2.style.opacity = '0.5';

            line3.style.backgroundColor = '#333333';
            line3.style.opacity = '0.5';

            line4.style.backgroundColor = '#2D9CDB';
            line4.style.opacity = '1';
            line5.style.backgroundColor = '#333333';
            line5.style.opacity = '0.5';
            line6.style.backgroundColor = '#333333';
            line6.style.opacity = '0.5';
    } else if(scrolled > 64 && scrolled < 80) {
      line1.style.backgroundColor = '#333333';
      line1.style.opacity = '0.5';

      line2.style.backgroundColor = '#333333';
      line2.style.opacity = '0.5';

      line3.style.backgroundColor = '#333333';
      line3.style.opacity = '0.5';

      line4.style.backgroundColor = '#333333 ';
      line4.style.opacity = '0.5';
      line5.style.backgroundColor = '#2D9CDB';
      line5.style.opacity = '1';
      line6.style.backgroundColor = '#333333';
      line6.style.opacity = '0.5';
} else if(scrolled > 80) {
  line1.style.backgroundColor = '#333333';
  line1.style.opacity = '0.5';

  line2.style.backgroundColor = '#333333';
  line2.style.opacity = '0.5';

  line3.style.backgroundColor = '#333333';
  line3.style.opacity = '0.5';

  line4.style.backgroundColor = '#333333';
  line4.style.opacity = '0.5';
  line5.style.backgroundColor = '#333333';
  line5.style.opacity = '0.5';
  line6.style.backgroundColor = '#2D9CDB';
  line6.style.opacity = '1';
}


}


window.onscroll = function() {myFunction()};



let openModalBtn = document.querySelectorAll('.open-modal-js');
let modalWindow = document.querySelector('.modal-outer');
let closeModal = document.querySelector('.close');
let closeModalLink = document.querySelector('.close-btn');

openModalBtn.forEach((btn) => {
    btn.addEventListener('click', (e) => {
        modalWindow.classList.add('__visible');
    })
})

closeModal.addEventListener('click', (e) => {
    modalWindow.classList.remove('__visible');
})

closeModalLink.addEventListener('click', (e) => {
  modalWindow.classList.remove('__visible');
})




$(document).ready(function(){
  // Добавить плавную прокрутку до всех ссылок
  $("a").on('click', function(event) {

      // Убедись в этом что .hash имеет значение перед переопределением поведения по умолчанию
      if (this.hash !== "") {
          // Запретить поведение щелчка якоря по умолчанию
          event.preventDefault();

          // Хранить хэш
          var hash = this.hash;

          // Использование метода animate() jQuery для добавления плавной прокрутки страницы
          // Необязательное число (800) указывает количество миллисекунд, необходимых для прокрутки до указанной области
          $('html, body').animate({
              scrollTop: $(hash).offset().top
          }, 800, function(){

              // Добавить хэш (#) для URL-адреса после завершения прокрутки (поведение щелчка по умолчанию)
              window.location.hash = hash;
          });
      } // Конец, если
  });
});